.contact{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
}

form{
    max-width: 550px;
    width: 100%;
    background-image: url(C:\Users\jp\Documents\Nav\my-first-react\src\pages\my-bg-about.jpeg);
    background-size: cover;
    padding: 20px 20px 20px;
    border-radius: 25px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    margin: 25px;
}

form h2{
    font-size: 30px;
    text-align: center;
    color: azure;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

form .input-box{
    margin-top: 20px;
}

.input-box .field{
    width: 100%;
    height: 45px;
    background: transparent;
    border: 1px solid;
    outline: none;
    border-radius: 25px;
    padding: 15px;
    font-size: 15px;
    color: azure;
    margin-top: 8px;
    
}

.input-box .field.msg{
    height: 100px;
    resize: none;
}

form button{
    width: 100%;
    height: 55px;
    background: #16423C;
    color: azure;
    border: none;
    border-radius: 25px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    cursor: pointer;
    font-size: 13.5px;
    font-weight: 500;
    margin-top: 25px;
}

form button:hover{
    background: #117554;
    color: black;
}