@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

*{
    box-sizing: border-box;
    font-family: "Poppins",sans-serif;
}

body{
    background-image: url(C:\Users\jp\Documents\Nav\my-first-react\src\pages\my-bg.jpeg);
    background-size: cover;
}

.container{
    margin: 1rem;
    text-align: center;
}

.nav{
    color: #001F3F;
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    padding: 0 5rem;
    height: 75px;
    font-weight: bold;
}

.title{
    font-size: 1.5rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
    width: 100px;
    justify-content: center;
}

.nav li.active{
    font-size: 15px;
}

.nav li.active:hover{
    font-size: 20px;
    transition: .3s;
}