.h-div{
    padding: 100px 0;
}

.h-div p{
    font-size: 15px;
    color: #16423C;
}

.my-h1{
    font-family: "Bebas Neue", sans-serif;
    font-weight: 700;
    font-size: 65px;
    color: #114232;
    margin-bottom: 5px;
}

.my-h1 span{
    font-family: "Bebas Neue", sans-serif;
    font-weight: 700;
    font-size: 65px;
    color: #914F1E;
}

.h-button{
    font-size: 15px;
    border-radius: 25px;
    border-width: 0;
    width: 150px;
    height: 50px;
    color: azure;
    background-color: #114232;
}

.h-button:hover{
    background: #433520;
    color: #F7DCB9;
    transition: .3s;
    cursor: pointer;
}
